.cover-login {
	background: url('../images/home/banner.jpg');
	background-size: cover;
	background-repeat: no-repeat;
	height: 100vh;
	i:hover {
		color: #0000ff; }
	h2 {
		font-family: 'Shadows Into Light', cursive;
		font-size: 40px;
		font-weight: bolder;
		text-align: center;
		color: #fff;
		margin: 50px auto; }
	& > .container {
		background-color: rgba(0,0,0,0.8);
		height: 100vh;
		width: 100vw; }
	#loginpanel {
		padding: 10px;
		h3 {
			font-size: 20px;
			font-weight: bold;
			padding: 5px;
			margin: 10px 10px;
			text-shadow: 1px 1px #000; }
		.form-group {
			margin-bottom: 20px;
			.form-control {
				padding: 5px 10px;
				font-size: 20px; } }
		.btn {
			padding: 10px;
			font-size: 20px;
			font-weight: bold; }
		p {
			text-align: center;
			font-size: 18px;
			margin-top: 10px;
			a {
				font-weight: bold; }
			a:hover {
				text-decoration: none;
				color: #ffa500; } } } }
