#registerform {
	// background: url('../images/home/loginregister.jpg')
	// background-size: cover
	// background-repeat: no-repeat
	height: 100vh;
	margin: -20px;
	.main {
		text-shadow: 1px 1px #000;
		margin-top: 70px;
		.panel-heading {
			text-align: center;
			color: #fff;
			a:hover {
				color: blue; }
			h2 {
				font-size: 40px;
				font-weight: bolder;
				font-family: 'Philosopher'; } }
		.main-login {
			background-color: #9e6500;
			border-radius: 2px;
			box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
			margin-top: 30px;
			margin: 0 auto;
			max-width: 350px;
			padding: 40px 40px; }
		.form-group {
			margin-bottom: 15px;
			label {
				margin-bottom: 10px;
				font-size: 20px;
				color: #fff; }
			input {
				font-size: 18px;
				padding: 5px; }
			button {
				margin-top: 5px;
				font-weight: bold; } }
		.checklogin {
			font-family: 'Philosopher';
			text-align: center;
			margin: 20px auto;
			font-size: 25px;
			font-weight: bolder;
			color: #fff;
			a {
				font-weight: bolder;
				font-size: 15px;
				color: #000;
				text-shadow: none; }
			a:hover {
				text-decoration: none; } } } }
